<template>
  <div class="bc-user-modal-layer">
    <div class="user-modal-layer-conetne">
      <div class="user-modal-layer-conetne-header">
        <a href="javascript:;" @click="closeModal">
          <i class="el-icon-close"></i>
        </a>
      </div>
      <div class="user-modal-layer-conetne-main">
        修改用户积分：
        <el-input v-model="scores" placeholder="请输入积分"></el-input>
        <p class="modal-layer-conetne-main-disable">
          是否禁用：
          <el-radio v-model="is_disable" label="1">是</el-radio>
          <el-radio v-model="is_disable" label="0">否</el-radio>
        </p>

        <el-button type="primary" @click="subModify">确定</el-button>
      </div>
    </div>
  </div>
</template>


<script>
import * as ajax from '@/api'

export default {
  data(){
    return{
      scores: 0,
      is_disable: '0',
    }
  },
  props:['userData'],   
  methods:{
    closeModal(){
      this.$emit('close') //关闭模态层
    },
    subModify(){
      let is = this.is_disable == '1' ? true : false
      ajax.modifyPeopleData(this.userData.id,this.scores,is).then(resp=>{
        if(resp.data.code){
          this.closeModal()
        }
      })

      //提交修改
    }
  },
  created(){
    this.scores = this.userData['scores'] //初始化积分
    this.is_disable = this.userData['is_disable']=='是' ? '1' : '0' //初始化是否禁用
  }
};
</script>


<style lang='scss'>
.bc-user-modal-layer {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  background-color: rgba(27, 25, 25, 0.5);
  z-index: 99;
  .user-modal-layer-conetne {
    width: 500px;
    height: 410px;
    background-color: #fff;
    position: absolute;
    margin: auto;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 9999999;
    &-header {
      height: 50px;
      border-bottom: 1px solid #999;
      a {
        float: right;
        font-size: 30px;
        margin-right: 10px;
        margin-top: 5px;
        color: #666;
      }
    }
    &-main{
      margin-top: 20px;
      padding-left: 20px;
      .el-input{
        .el-input__inner{
          width: 70%;
        }
      }
      .modal-layer-conetne-main-disable{
        margin-top: 20px;
      }
      .el-button--primary{
        margin-left: 188px;
        margin-top: 50px;
      }
    }
  }
}
</style>